.App {
  width: 100vw;
  overflow: hidden;
  text-align: center;
}

/** intro section*/

.App-intro {
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("./asset/intro-back.svg");
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-intro-text {
  font-size: 80px;
  width: 50%;
  line-break: normal;
  line-height: 90px;
  color: #ffffff;
  animation: intro-text-animation 1.5s;
  position: relative;
}

/**  education section */
.App-education {
  width: 100vw;
  height: 100vh;
  position: relative;
  background-image: url("./asset/education-back.svg");
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  left: -10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.App-education-heading {
  font-size: 75px;
  line-height: 90px;
  color: #7932d4;
  margin-top: 50px;
  position: relative;
}

.App-education-card {
  width: 600px;
  height: fit-content;
  background-color: #ffffff;
  border: 0.2px solid #000000;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
}

.App-education-card:hover{
background: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%); 
}



.App-education-card-heading {
  font-size: 50px;
  line-height: 55px;
  color: #000000;
}

.App-education-card-clgname {
  font-size: 40px;
  line-height: 42px;

  color: #6a3ca4;
}

.App-education-card-year {
  font-size: 30px;
  line-height: 32px;
  color: #000000;
}

/** animation classes */
.slide-up {
  animation: slide-up 3s;
}
.slide-up-skills {
  animation: slide-up 4s;
}

.slide-up-skill-card {
  position: relative;
  animation: slide-up-fade 2s;
}

.slide-left {
  animation: slide-left 3s;
}

/** skill section */

.App-skills {
  width: 100vw;
  height: 100vh;
  position: relative;
  top: -4px;
}

.App-skill-upper {
  width: 100vw;
  height: 40%;
  padding-top: 250px;
  background: #77388c;
}

.App-skill-heading {
  font-size: 75px;
  line-height: 90px;
  color: white;
}

.App-skill-card-group {
  position: absolute;
  display: flex;
  justify-content: center;
  top: 400px;
  width: 100%;
  left: auto;
}

.App-skill-card {
  /* width: 400px; */
  width: 30%;
  max-width: 400px;
  background: #ffffff;
  border: 0.01px solid #6e698d;
  box-sizing: border-box;
  
}



.App-skill-card-heading {
  color: #000000;
  font-size: 40px;
  line-height: 45px;
  border-bottom: 0.1px solid black;
  border-radius: 20%;
}

.App-skill-card-content {
  color: #ae4abe;
  font-size: 30px;
  padding: 20px;
  line-height: 40px;
}

/** font design */
.font-design1 {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  text-align: center;
}

/** my-work section */

.App-my-work {
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App-my-work-info-group {
  margin-top: 150px;
}

.App-my-work-card-group {
  display: flex;
  width: 80vw;
  margin-top: 100px;
  flex-wrap: wrap;
  justify-content: space-around;
}

.App-my-work-card {
  width: 300px;
  height: 300px;
  border-radius: 10%;
}

/** what-i-do */

.App-what-i-do{
  width: 100vw;
  height: fit-content;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 300px;
}

.App-what-i-do h2{
  font-size: 50px;
  color: #ae4abe;
}
.App-what-i-do button{
  width: 200px;
  height: 50px;
  background-color: #ae4abe;
  border: 0px;
  
}

.App-what-i-do a{
  width: 100%;
  height: 100%;
  color: #ffffff;
  font-size: 20px;
  text-decoration: none;
}

.App-what-i-do img{
  width: 300px;
  height: 300px;
}

/** footer */

.App-footer{
  width: 100vw;
  display: flex;
  padding-bottom: 100px;
  background-color: #7510f7;
  height: 400px;
  margin-top: 400px;
}
.App-developerimage{
  width: 30vw;
  height: 30vw;
  min-width: 300px;
  min-height: 300px;
  opacity: 1;
  background-image: url("./asset/devloper.svg");
  background-size: cover;
  background-repeat: no-repeat;
  margin-left: auto;
  margin-right: 100px;
  margin-top: -100px;
}

.App-footer-group{
  display: flex;
  flex-direction: column;
  align-self: center;
  margin-left: 100px;
  
}

.App-footer-text{
  font-size: 40px;
}

.App-footer-contact-group{
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-top: 100px;
}


/** animaions */

@keyframes intro-text-animation {
  from {
    top: -1000px;
  }
  to {
    top: 0px;
  }
}

@keyframes slide-up-fade {
  from {
    top: 1000px;
    /* left : 1000px; */
    opacity: 0;
    transform: rotate(300deg);
  }
  to {
    top: 0px;
    /* left : 0px; */
    opacity: 1;
    transform: rotate(0deg);
  }
}

@keyframes slide-up {
  from {
    top: 1000px;
  }
  to {
    top: 0px;
  }
}

@keyframes slide-left {
  from {
    left: 1000px;
    opacity: 0;
  }
  to {
    left: 0px;
    opacity: 1;
  }
}




