.imageclass{
    width: 100%;
    height: 100%;
}

.project--card--main--container{
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
}

.project--card-first-anim{
    animation : anim22 2s;   

}



@keyframes anim22 {
    from{
        transform: rotateY(180deg);
    }    
    to{
        transform: rotateY(0deg);

    }
}

.project--card--second--container{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}


.project--card--desc{
    font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  text-align: center;
  color: white;
  font-size: 30px;
}

.project--card--second--container a{
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    text-align: center;
    color: black;
    font-size: 30px;
    text-decoration: none;
  }

.project--card--website{
    padding: 10px;
    border-radius: 30%;
    background-color: white;
}
  